@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
:root {
  --gray: #707070;
  --dark-gray: #404040;
  --white: #ffffff;
  --black: #000000;
  --yellow: #f9d71c;
  --red: #ff0000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}

.bi {
  /* font-size: 2.5rem; */
  color: #000000;
  color: var(--black);
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
}

